<template>
    <div class="c_deploy">
        <!-- <el-dialog title="权限管理" :visible.sync="visible" @colse="$emit('update:show',false)" :show="show" width="690px"> -->
        <el-dialog :title="title" :visible.sync="visible" @close="$emit('update:dialogShow', false)" :dialogShow="dialogShow" width="690px">
            <!-- 分页内容区域 -->
            <div class="c_dialog">
                <div class="c_dialog_left">
                    <div class="c_dialog_left_top">
                        <!-- <el-input class="c_dialog_left_input" placeholder="请输入内容">
                            <i slot="prefix" class="el-input__icon el-icon-search"></i>
                        </el-input> -->
                        <div class="c_dialog_input">
                            <img class="c_dialog_icon" :src="require('../assets/img/power/search.png')" alt>
                            <el-input placeholder="输入关键字"></el-input>
                        </div>
                        <el-button class="c_dialog_left_btn" icon="el-icon-plus"></el-button>
                    </div>
                    <!-- 树形菜单区域 -->
                    <div class="c_tree_menu">
                        <!-- 接收的： menuList  写死的：  corpList :corpData="menuList" -->
                        <!-- <DepTree @item="clickDep" @loadMore="clickMore"  /> -->
                        <DepTree :corpTreeId="corpTreeId" :searchType="searchType" />
                    </div>
                </div>
                <div class="c_dialog_rigth">
                    <div class="c_dialog_rigth_title">{{twoTitle}}</div>
                    <div class="c_dialog_rigth_item">
                        <div v-for="(item,index) in powerList" :key="item.id" class="c_list">
                            <div class="c_list_text">
                                <img v-if="item.type === 'dep'" class="c_list_text_icon" :src="require('../assets/img/power/dep.png')" alt="">
                                <img v-else class="c_list_text_icon" :src="require('../assets/img/power/person.png')" alt="">
                                {{item.name}}
                            </div>
                            <!-- <div class="c_list_icon">
                            <i class="el-icon-delete"></i>
                        </div> -->
                            <el-button @click="delPowerList(index)" class="c_list_icon" type="text">
                                <i class="el-icon-delete"></i>
                            </el-button>
                        </div>
                    </div>
                </div>
            </div>
            <!-- 底部 -->
            <span slot="footer" class="dialog-footer">
                <el-button class="c_footerbtn" type="primary" @click="submitBtn">提交</el-button>
                <el-button class="c_footerbtn" @click="resetBtn">重置</el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
    import DepTree from "./c_departmentTree.vue";
    import { mapGetters, mapState } from 'vuex'
    export default {
        data() {
            return {

                visible: this.dialogShow,
                // 部门数据
                corpList: [{}], // 顶级菜单列表
                corpDepList: [{}], //子级菜单列表
                selectList: [], //后选中权限
                deleteList: [], //删除选中权限
            };
        },
        props: { //接收父组件传值

            dialogShow: { //是否弹框
                type: Boolean,
                default: false
            },
            powerList: { //已选权限
                type: Array,
                default: []
            },
            title: {
                type: String,
                default: '权限管理'
            },
            twoTitle: {
                type: String,
                default: '用户将拥有以下权限'
            },
            isUser: { //是否只选择人，默认是false
                type: Boolean,
                default: false
            },
            isOne: { //是否只选择当前选中，默认是false
                type: Boolean,
                default: false
            },
            corpTreeId: {
                type: String,
                default: 'aa2' // 集团树id 默认aa2
            },
            searchType: {
                type: String,
                default: 'all'
            }
        },

        watch: {
            dialogShow() {
                this.visible = this.dialogShow;
            }
        },
        methods: {
            delPowerList(index) { //删除已选权限
                // this.$emit('delete',index)
                // console.log();
                this.deleteList.push(this.powerList.splice(index, 1)[0])
                console.log(this.deleteList);
            },

            /**
             * 点击返回按钮给父组件传值
             */
            submitBtn() {
                // console.log('1111');
                let allList = {
                    powerList: this.powerList,
                    deleteList: this.deleteList,
                    selectList: this.selectList
                }

                this.$emit('submit', allList);

            },
            resetBtn() { //重置按钮
                this.$emit('reset');
            },


            // 默认加载函数
            clickEmitFn(obj) {
                let {
                    item,
                    searchType,
                    corpTreeId
                } = obj;
                // console.log(item);
                // console.log('corpidsjfsd');
                // console.log(corpTreeId);
                // 执行默认的函数

                // 可以点击好几个
                // 可以在这里区分 是只可以点击部门 还是只可以点击人 
                // 默认的既可以点部门也可以点人
                if (corpTreeId == 'aa2') {
                    return this.aa2DetaultFn(obj);
                } else if (corpTreeId == 'ad12') {
                    // 这个是只可以选择部门，这个
                    // 设置所在部门
                    this.setUserInDep(obj);

                }
            },

            // 调整部门执行的函数
            setUserInDep(obj) {
                let {
                    item,
                    searchType,
                    corpTreeId
                } = obj;
                // 覆盖掉选择的
                this.powerList.pop();
                this.powerList.push(item);
            },


            // 默认aa2应该走的函数
            aa2DetaultFn(obj) {
                let {
                    item,
                    searchType,
                    corpTreeId
                } = obj;
                for (let i = 0; i < this.powerList.length; i++) {
                    if (this.powerList[i].depid === item.depid) {
                        this.$zero.ToastError("已选择");
                        return
                    }
                }
                //只选择人，不选择部门
                if (this.isUser) {
                    if (item.type == 'user') {
                        this.powerList.push(item)
                    }
                    return;
                }
                //只选择当前选中
                if (this.isOne) {
                    this.powerList.push(item)
                    return;
                }
                // this.powerList.push(item)

                let allData = JSON.parse(sessionStorage.getItem('allData'))
                console.log(allData);
                let cheshilist = [];

                function clickUserAddParentDep(arr, parent_id) {
                    for (let i = 0; i < arr.length; i++) {
                        if (arr[i].depid === parent_id) {
                            cheshilist.unshift(arr[i])
                            clickUserAddParentDep(arr, arr[i].parent_id)
                            break;
                        }

                    }
                }
                clickUserAddParentDep(allData, item.parent_id)
                // console.log(111111);

                console.log(cheshilist);
                let arr = JSON.parse(JSON.stringify(this.powerList))
                let teamList = [];
                let userList = [];
                for (let i = 0; i < arr.length; i++) {
                    if (arr[i].type == 'dep') {
                        teamList.push(arr[i])
                    } else {
                        userList.push(arr[i])
                    }

                }

                for (let i = 0; i < cheshilist.length; i++) {
                    let isok = true
                    for (let j = 0; j < arr.length; j++) {
                        if (cheshilist[i].depid === arr[j].depid) {
                            isok = false
                            break;
                        }
                    }
                    if (isok) {
                        // this.powerList.push(cheshilist[i])
                        this.selectList.push(cheshilist[i])
                        teamList.push(cheshilist[i])
                    }

                }
                // console.log();

                if (item.type == 'dep') {
                    teamList.push(item)
                } else {
                    userList.push(item)
                }

                console.log(this.powerList.splice(0, this.powerList.length));
                console.log(teamList.concat(userList));
                // console.log(this.powerList.push(teamList.concat(userList)));
                let list = teamList.concat(userList);
                for (let i = 0; i < list.length; i++) {
                    this.powerList.push(list[i]);
                }
                // return;
                this.selectList.push(item)
            },
        },
        mounted() {
            // 监听点击集团树文字
            this.$zero.eventBus.$on("departmentItem", this.clickEmitFn);
            // this.$store.dataManagement.getters.selectPower(this.powerList)
            console.log(this.selectPower);
        },
        computed: {
            // ...mapState({
            //     checkoutStatus: state => state.cart.checkoutStatus
            // }),
            ...mapGetters('dataManagement', {
                selectPower: 'selectPower',
                // total: 'cartTotalPrice'
            })
        },

        beforeDestroy() {
            // 卸载监听集团树文字
            this.$zero.eventBus.$off("departmentItem", this.clickEmitFn);
        },
        components: {
            DepTree
        }
    };
</script>

<style scoped>
    .c_deploy .el-dialog__header {
        border-bottom: 1px solid #000;
    }

    .c_deploy .el-dialog__body {
        padding-top: 15px;
        padding-bottom: 55px;
    }

    /* 弹框内容区域 */
    .c_dialog {
        width: 100%;
        height: 520px;
        display: flex;
        justify-content: space-between;
    }

    .c_dialog_left {
        width: 385px;
        height: 520px;
        overflow: hidden;
    }

    .c_dialog_left_top {
        display: flex;
        margin-bottom: 12px;

    }

    .c_dialog_input {
        flex-grow: 1;
        position: relative;
    }

    .c_dialog_icon {
        position: absolute;
        left: 15px;
        top: 12px;
        z-index: 9;
        width: 16px;
        height: 16px;
    }


    .c_dialog_left_btn {
        padding: 6px;
        margin-left: 10px;
        border-color: #2f78ef;
        color: #2f78ef;
        font-size: 24px;
    }

    /* 树形菜单区域 */
    .c_tree_menu {
        width: 100%;
        height: 90%;
        background-color: #f2f2f2;
        overflow: auto;
    }

    /* 分页右侧区域 */
    .c_dialog_rigth {
        width: 245px;
        height: 520px;
        border-left: 1px solid #e6e6e6;
        padding-left: 20px;
        box-sizing: border-box;
        overflow: auto;
    }

    .c_dialog_rigth_title {
        font-size: 14px;
        color: #333;
        font-weight: bold;
        margin-bottom: 5px;
        height: 20px;
    }

    .c_dialog_rigth_item {
        width: 100%;
        height: 490px;
        overflow: auto;
    }

    .c_list {
        display: flex;
        justify-content: space-between;
        padding-top: 10px;
        font-size: 14px;
    }

    .c_list_text {
        color: #4d4d4d;
        display: flex;
        align-items: center;
    }

    .c_list_text_icon {
        width: 14px;
        height: 14px;
        margin-right: 5px;
    }


    .c_list_icon {
        padding: 0;
        color: #59514f;
    }

    /* 分页底部 */
    .c_footerbtn {
        padding: 12px 30px;
        border-radius: 0px;
        font-weight: bold;
    }


    /* .c_deploy  .el-dialog__header {
        border-bottom: 1px solid #d9d9d9;
        font-weight: bold;
    }

    .c_deploy  .el-dialog__body {
        padding-top: 15px;
        padding-bottom: 55px;
    }

    .c_deploy  .el-dialog__footer {
        border-top: 1px solid #d9d9d9;
        padding: 15px 20px;
    }

    .c_deploy .c_dialog_input .el-input__inner {
        text-indent: 18px;
        border: 1px solid #e1e1e1;
    } */
</style>