<template>
    <!-- 
  数据中心下拉菜单只显示部门传入参数 searchType = 'dep'
  -->
    <div class="c_depart_tree">
        <!-- @item="clickDep" -->
        <!--  @addBookLoadMore="addBookLoadMore" -->
        <DepTree :isShowIcon="isShowIcon" :corpTreeId="corpTreeId" :searchType="searchType" :isUseSpot="isUseSpot" :corpData="showData" />
    </div>
</template>
<script>
    import DepTree from "./c_departmentsList.vue";
    export default {
        props: {
            searchType: {
                type: String,
                default: "all"
            },
            isShowIcon: {
                type: Boolean,
                default: true
            },
            corpTreeId: {
                type: String,
                default: '1'
            },
            isUseSpot: {
                type: Boolean,
                default: false
            }
        },
        data() {
            return {
                // 部门数据
                // 定义列表格式
                corpList: [{}], // 企业通讯录列表
                corpDepList: [{}],
                allData: []  //所有的树状数据
                // corpList: [
                //   {
                //     company_id: "123", // 企业 id
                //     depid: 38196,
                //     show: false,
                //     name: "深圳华龙讯达信息技术股份有限公司",
                //     children: [
                //       {
                //         name: "移动子部门",
                //         depid: "69550",
                //         tyep: "dep",
                //         show: false,
                //         children: [
                //           {
                //             name: "移动子部门2",
                //             depid: "32475",
                //             show: false,
                //             tyep: "dep"
                //           },
                //           {
                //             name: "移动子部门3",
                //             depid: "18649",
                //             show: false,
                //             tyep: "dep"
                //           }
                //         ]
                //       },
                //       {
                //         name: "移动子部门2",
                //         depid: "32475",
                //         show: false,
                //         tyep: "dep",
                //         children: [
                //           {
                //             name: "移动子部门2",
                //             depid: "3254",
                //             show: false,
                //             tyep: "dep"
                //           }
                //         ]
                //       },
                //       {
                //         name: "移动子部门3",
                //         depid: "18649",
                //         show: false,
                //         tyep: "dep"
                //       },
                //       {
                //         name: "统计测试",
                //         userid: "tongji",
                //         show: false,
                //         tyep: "dep"
                //       }
                //     ]
                //   }
                // ]
            };
        },

        computed: {
            showData() {
                if (this.searchType === "all") {
                    return this.corpList;
                } else if (this.searchType === "dep") {
                    return this.corpDepList;
                }
            }
        },

        methods: {
            // 获取用户公司
            async getCompony() {
                let result = await this.$api.addressBook.getCompony();
                // console.log(result);
                
                if (result.statusCode === 72305) {
                    this.$zero.ToastWarning("用户或分组不存在");
                } else if (result.statusCode === 72315) {
                    this.$zero.ToastWarning("请核对公司id");
                } else if (result.statusCode === 75500) {
                    this.$zero.ToastWarning("服务异常，请稍后再试");
                }

                if(result.statusCode !== 75200){
                    return this.$zero.ToastWarning(`获取集团信息失败，错误码${result.statusCode}`);
                }
                
                // 请求成功
                // this.$zero.ToastSuccess("获取成功");
                let corpInfo = result.list[0];
                // this.corpList[0].depid = corpInfo.depid;
                // this.corpList[0].name = corpInfo.name;
                // 向 data 中添加数据
                if (this.searchType === "all") {
                    this.$set(this.corpList[0], "isCorp", true);
                    this.$set(this.corpList[0], "depid", corpInfo.depid);
                    this.$set(this.corpList[0], "name", corpInfo.name);
                    this.$set(this.corpList[0], "show", false);
                    this.$set(this.corpList[0], "children", []);
                    this.$set(this.corpList[0], "isShowUpdate", false); // 是否展示修改的
                    this.$set(this.corpList[0], "_id", corpInfo._id);
                } else if (this.searchType === "dep") {
                    this.$set(this.corpDepList[0], "isCorp", true);
                    this.$set(this.corpDepList[0], "depid", corpInfo.depid);
                    this.$set(this.corpDepList[0], "name", corpInfo.name);
                    this.$set(this.corpDepList[0], "show", false);
                    this.$set(this.corpDepList[0], "children", []);
                    this.$set(this.corpDepList[0], "isShowUpdate", false); // 是否展示修改的
                    this.$set(this.corpDepList[0], "_id", corpInfo._id);

                    // 在这里给 通讯录 addressBook.vue 文件传递一个事件 告诉他企业的根部门的id
                    if(this.corpTreeId === 'ad10'){
                        this.$zero.eventBus.$emit('tellCorpDep', {
                            isCorp: true,
                            depid: corpInfo.depid,
                            name: corpInfo.name,
                            show: false,
                            children: [],
                            isShowUpdate: false,
                            _id: corpInfo._id
                        });
                    }
                }
                // 获取完成之后自动的自己去加载首页部门
                this.depLoadMore({
                    depid: corpInfo.depid
                });
                
            },

            /**
             * 点击 部门 或者 + 号 去加载更多
             *  在此处判断是 加载部门 还是加载所有
             **/
            async depLoadMore(item, searchType = "all") {
                
                if (this.searchType === "dep" || searchType === "dep") {
                    this.loadDep(item);
                    return;
                }
                // console.log("请求的是加载所有的");
                // 请求所有的人和部门
                const itemDepId = item.depid;
                if (!itemDepId) {
                    return this.$zero.ToastError("无效的部门id");
                }
                // 根据获取到的部门 id 拿取信息数据
                let result = await this.$api.addressBook.getDepAllMemeber({
                    depid: itemDepId
                });

                // console.log("获取到的部门信息数据");
                // console.log(result);
                
                if (result.statusCode === 72315) {
                    return this.$zero.ToastError("请核对公司id");
                } else if (result.statusCode === 75500) {
                    return this.$zero.ToastError("服务异常，请稍后再试");
                } else if (result.statusCode === 75200) {
                    var children = [];
                    for (let i = 0; i < result.list.length; i++) {
                        children[i] = {
                            name: result.list[i].name,
                            type: result.list[i].type,
                            depid: result.list[i].depid,
                            show: false, // 是否展开
                            parent_id: itemDepId,  //上级id
                            isShowUpdate: false, // 是否展示修改的
                            _id: result.list[i]._id
                        };
                        if (result.list[i].type === "user") {
                            children[i].depid = result.list[i].userid;
                        } else if (result.list[i].type === "dep") {
                            children[i].children = [];
                        }
                    }

                    // console.log('this.corpList'+this.corpList);
                    //获得总数据
                    console.log('集团组织架构');
                    console.log(this.corpList);
                    let isAdd = true;
                    if(this.allData.length > 0 && children.length > 0){
                        for (let i = 0; i < this.allData.length; i++) {
                            if(this.allData[i].depid === children[0].depid){
                                isAdd = false;
                                break;
                            }
                            
                        }
                    }
                    if(isAdd){
                        this.allData = this.allData.concat(children);
                    }
                    
                    // console.log(this.allData);
                    sessionStorage.setItem('allData',JSON.stringify(this.allData))

                    function recursiveFindItem(obj, itemDepId) {
                        for (var key in obj) {
                            if (typeof obj[key] === "object") {
                                recursiveFindItem(obj[key], itemDepId);
                            } else {
                                if (obj[key] === itemDepId) {
                                    obj.show = !obj.show;
                                    // obj.isClass = true;
                                    return (obj.children = children);
                                }
                            }
                        }
                    }
                    // 递归循环将数据改变
                    recursiveFindItem(this.corpList, itemDepId);
                    
                }
            },

            // 加载部门
            async loadDep(item) {
                console.log('我在那、、、、、111');
                const itemDepId = item.depid;
                if (!itemDepId) {
                    return this.$zero.ToastError("无效的部门id");
                }
                // 根据获取到的部门 id 拿取信息数据
                let result = await this.$api.addressBook.getChildrenDep({
                    depid: itemDepId
                });
                // console.log('获取到的部门列表');

                // console.log("获取到的部门信息数据");
                // console.log(result);
                if (result.statusCode === 72315) {
                    return this.$zero.ToastError("请核对公司id");
                } else if (result.statusCode === 75500) {
                    return this.$zero.ToastError("服务异常，请稍后再试");
                } else if (result.statusCode === 75200) {
                    // console.log('走到了这里吧！！');
                    // console.log(result);
                    var children = [];
                    for (let i = 0; i < result.list.length; i++) {
                        children[i] = {
                            name: result.list[i].name,
                            type: "dep",
                            depid: result.list[i].depid,
                            show: false, // 是否展开
                            parent_id: itemDepId,  //上级id
                            isShowUpdate: false, // 是否展示修改的这个是点击那三个点显示
                            _id: result.list[i]._id
                        };
                        children[i].children = [];
                    }

                    console.log('集团组织架构');
                    console.log(this.corpDepList);
                    


                    function recursiveFindItem(obj, itemDepId) {
                        for (var key in obj) {
                            if (typeof obj[key] === "object") {
                                recursiveFindItem(obj[key], itemDepId);
                            } else {
                                if (obj[key] === itemDepId) {
                                    obj.show = !obj.show;
                                    return (obj.children = children);
                                }
                            }
                        }
                    }

                    // 递归循环将数据改变
                    recursiveFindItem(this.corpDepList, itemDepId);
                }
            },

            /**
             * 点击文字的时候触发 为了卸载函数的时候取消该事件
             */
            clickTxtEmit(obj){
                let {item, searchType, corpTreeId} = obj;
                // 只有他们是同一个东西才去执行
                if(corpTreeId === this.corpTreeId){
                    if(corpTreeId === 'csf10'){
                        // 这个地方是选择单位，只能下拉一层，其余的不可以下拉了
                        return ;
                    }
                    // console.log('触发了1次')
                    this.depLoadMore(item, searchType);
                }
            },

            /**
             * 递归删除 修改部门名称 增加部门集团树中的某一个部门
             */
            recursionDelDep({corpData, depid, type, name, newDep}){
                for(let i=0;i<corpData.length;i++){
                    if(corpData[i].depid === depid){
                        if(type === 'del'){
                            // 删除
                            corpData.splice(i, 1);
                        }else if(type === 'change'){
                            // 修改名称
                            corpData[i].name = name;
                        }else if(type === 'add'){
                            // 新增
                            if(corpData[i].children){
                                corpData[i].children.push(newDep);
                            }else{
                                corpData[i].children = [];
                                corpData[i].children.push(newDep);
                            }
                        }
                        return ;
                    }
                    if(corpData[i].children.length){
                        this.recursionDelDep({corpData: corpData[i].children, depid, type, name, newDep});
                    }
                }
            },
            
            // 删除某一个部门
            ad10Del(item){
                // 目前只有 ad10 支持删除增加部门修改部门名称
                if(this.corpTreeId !== 'ad10'){
                    return ;
                }
                let {depid} = item;
                this.recursionDelDep({
                    depid: depid,
                    corpData: this.corpDepList,
                    type: 'del'
                });
            },

            // 修改部门名称函数
            ad10changeDep({depid, name}){
                // 目前只有 ad10 支持删除增加部门修改部门名称
                if(this.corpTreeId !== 'ad10'){
                    return ;
                }
                // console.log('修改之前');
                // console.log(depid, name);
                this.recursionDelDep({
                    type: 'change',
                    corpData: this.corpDepList,
                    depid: depid,
                    name: name
                });
                // console.log('修改之后');
            },

            // 增加新部门
            ad10Add(obj){
                // 目前只有 ad10 支持删除增加部门修改部门名称
                if(this.corpTreeId !== 'ad10'){
                    return ;
                }
                this.recursionDelDep({
                    newDep: obj.depinfo,
                    corpData: this.corpDepList,
                    depid: obj.depinfo.parentId,
                    type: 'add'
                });
            },
        },

        created() {
            this.getCompony();
        },
        
        mounted() {
            // 先去卸载再来监听 以防止触发好多次
            // 监听的加载更多
            this.emitFu = this.$zero.eventBus.$on("addBookLoadMore", this.clickTxtEmit);

            // 监听删除部门部门 将部门从集团树中删除
            this.$zero.eventBus.$on('ad10Del', this.ad10Del);

            // 监听修改部门名称，将部门的名称从集团树中改变
            this.$zero.eventBus.$on('ad10changeDep', this.ad10changeDep);

            // 监听添加子部门
            this.$zero.eventBus.$on('ad10Add', this.ad10Add);
        },
        beforeDestroy(){
            this.$zero.eventBus.$off("addBookLoadMore", this.clickTxtEmit);
            // 卸载删除部门部门 将部门从集团树中删除
            this.$zero.eventBus.$off("ad10Del", this.ad10Del);
             // 监听卸载修改部门名称，将部门的名称从集团树中改变
            this.$zero.eventBus.$off('ad10changeDep', this.ad10changeDep);
            // 卸载监听添加子部门
            this.$zero.eventBus.$off('ad10Add', this.ad10Add);
        },
        components: {
            DepTree
        }
    };
</script>
<style>
.c_depart_tree{
    padding-right: 25px;
}
</style>
